import React, { Component } from "react";

import style from "./StorySummary.module.css";

import ViewStoryBtn from "../../UI/Buttons/ViewStoryBtn/ViewStoryBtn";
import Stars from "../../UI/Stars/Stars";

class StorySummary extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <div className={style.StorySummary}>
        <div className={style.Header}>
          <div className={style.Caption}>
            <h1>{this.props.title}</h1>
            <Stars rating={this.props.rating} />
          </div>
          <div className={style.Data}>
            <h4>{this.props.creator}</h4>
            <h5>{this.props.views} views</h5>
          </div>
        </div>
        <div className={style.Content}>
          <div className={style.Image}>
            <img
              src={`http://localhost:8080/${this.props.img}`}
              alt="slika"
            />
          </div>
          <div className={style.TextAndBtn}>
            <div className={style.Text}>{this.props.summary}</div>
            <ViewStoryBtn clicked={this.props.clicked} />
          </div>
        </div>
      </div>
    );
  }
}

export default StorySummary;
